body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.product {
  max-width: 200px;
}

.product__wrapper {
  border: 1px solid #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  padding: 32px 16px 16px;
  display: flex;
}

.product__img {
  justify-content: center;
  align-items: center;
  display: flex;
}

.product__img-img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.product__name {
  color: #060b35;
  margin: 0 0 4px;
  font-weight: 500;
}

.product__name, .product__price-title {
  font-size: 12px;
  line-height: 150%;
}

.product__description {
  color: #616070;
  margin: 0 0 16px;
}

.product__info {
  align-items: end;
  margin-bottom: 24px;
}

.product__description, .product__info-review {
  font-size: 10px;
  line-height: 140%;
}

.product__info-review {
  color: #060b35;
}

.product__info, .product__price {
  justify-content: space-between;
  display: flex;
}

.product__info-rate {
  display: flex;
}

.product__price {
  margin-bottom: 16px;
}

.product__price-title {
  color: #616070;
}

.product__price-count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.button {
  justify-content: center;
  align-items: center;
  display: flex;
}

.button__link {
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .25s;
  display: block;
}

.button__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

